
import {enableOverflow,resetall, convertirEsferasArrojasExcepto} from '../functions/global.js';
import {esferaGroup, animate, camera, scene, currentIntersect, mouse, sizes} from '../../script.js';
import {animationHover,observing} from '../functions/animations.js';
export class MouseListener {

    
    mouseHover() {
        let lastMouseX;
        let lastMouseY;

        // Valores de lmite
/*         const minPolarAngle = Math.PI / 4; // 45 grados
        const maxPolarAngle = Math.PI / 3; // 60 grados
        const minAzimuthAngle = -Math.PI / 8; // -22.5 grados
        const maxAzimuthAngle = Math.PI / 8; // 22.5 grados
 */
        window.addEventListener("mousemove", (_event)=>
        {
        mouse.x= _event.clientX / sizes.width * 2-1;
        mouse.y = - (_event.clientY / sizes.height) * 2 + 1;
        })
        
        function updateCameraPosition(event) {
            const mouseX = (event.clientX / window.innerWidth) * 2 - 1;
            const mouseY = -(event.clientY / window.innerHeight) * 2 + 1;
          
/*             const polarAngle = (maxPolarAngle - minPolarAngle) * mouseY + minPolarAngle;
            const azimuthAngle = (maxAzimuthAngle - minAzimuthAngle) * mouseX + minAzimuthAngle;
          
            const radius = 2;
            const offsetX = radius * Math.sin(polarAngle) * Math.cos(azimuthAngle);
            const offsetY = radius * Math.cos(polarAngle); */
          
            // Utiliza una interpolacin lineal para suavizar el movimiento de la cmara
/*             const lerpFactor = 0.1;
            camera.position.x += (offsetX * 0.1 - camera.position.x) * lerpFactor;
            camera.position.y += (offsetY * 0.1 - camera.position.y) * lerpFactor; */
          
/*             camera.lookAt(scene.position); */
          
            // Almacena las posiciones actuales del mouse para el prximo cuadro
            lastMouseX = mouseX;
            lastMouseY = mouseY;
          }
        window.addEventListener('mousemove', updateCameraPosition);


    }
    mouseClick() {

        window.addEventListener("click", (_event) => {
   

            if(currentIntersect && !observing)
            {
                
                switch(currentIntersect.object){
                    case esferaGroup.esfera1:    
                    
                    enableOverflow();
                    document.getElementById('section1').scrollIntoView({behavior: 'smooth'});    
                        convertirEsferasArrojasExcepto(esferaGroup.esfera1, esferaGroup.plane1, esferaGroup);
                        animationHover(esferaGroup.esfera1);
                        break;
            
                    case esferaGroup.esfera2:    
                    enableOverflow();
                    document.getElementById('section2').scrollIntoView({behavior: 'smooth'});
                        convertirEsferasArrojasExcepto(esferaGroup.esfera2, esferaGroup.plane2,esferaGroup);
                        animationHover(esferaGroup.esfera2);
                        break;
            
                    case esferaGroup.esfera3:    
                    enableOverflow();
                    document.getElementById('section3').scrollIntoView({behavior: 'smooth'});
                        convertirEsferasArrojasExcepto(esferaGroup.esfera3, esferaGroup.plane3,esferaGroup);
                        animationHover(esferaGroup.esfera3);
                        break;
            
                    case esferaGroup.esfera4:    
                    enableOverflow();
                    document.getElementById('section3').scrollIntoView({behavior: 'smooth'});
                        convertirEsferasArrojasExcepto(esferaGroup.esfera4, esferaGroup.plane4,esferaGroup);
                        animationHover(esferaGroup.esfera4);
                        break;
                     
                }
               
            }
            else {
                console.log("No hay objeto clickeado")
            }
            
            });
    }


}