import { gsap } from "gsap";
import {disableOverflow,resetall} from './global.js';
import {esferaGroup, animate, camera, scene} from '../../script.js';
export let observing = false;
export let isAnimating = true; 

export function animationIntro(){

    gsap.to(camera.position, {duration: 3, z: 10, ease: "power2.inOut", onComplete: () => {    
        esferaGroup.esfera1.visible=true;
        esferaGroup.esfera2.visible=true;
        esferaGroup.esfera3.visible=true;
        esferaGroup.esfera4.visible=true;
}});
}

//export function on hover position the camera infront of the esfera hovered
export function animationHover(esfera_hovered){
   
    observing = true;
    gsap.to(camera.position, {duration: 2, x: esfera_hovered.position.x, y: esfera_hovered.position.y, z: esfera_hovered.position.z+0.5, ease: "power2.inOut", 
    onStart: () => {observing = true;},
    onComplete: () => {    
  
        camera.lookAt(esfera_hovered.position);
        stopAnimation();
/*         setTimeout(() => { 
            esferaClickedAnimationOut();
        }
        , 5000); */
}});
//gsap to document.getElementsByClassName("webgl")[0].style.opacity = 0.5; 
document.getElementsByClassName("webgl")[0].style.opacity = 1; 
document.getElementsByClassName("parallax")[0].style.position = "relative";
document.getElementsByClassName("parallax")[1].style.position = "relative";
document.getElementsByClassName("parallax")[2].style.position = "relative";
gsap.to(document.getElementsByClassName("webgl")[0].style, {duration: 4, opacity: 0, ease: "power2.inOut", 
onComplete: () => {
    document.getElementsByClassName("webgl")[0].style.pointerEvents = "none";

}});

}

export function stopAnimation() {
    isAnimating = false;
}

export function startAnimation() {
    isAnimating = true;
    animate();
}


export function esferaClickedAnimationOut(){
    if(observing){
        //reset camera position and look at to scene.position usuing gsap
        gsap.to(camera.position, {duration: 2, x: 0, y: 0, z: 10, ease: "power2.inOut",
        onComplete: () => {
            camera.lookAt(scene.position);
            observing = false;

        }
        });
        gsap.to(document.getElementsByClassName("webgl")[0].style, {duration: 3, opacity: 1, ease: "power2.inOut", 
        onStart: () => {disableOverflow(); resetall(esferaGroup); startAnimation();},
        onComplete: () => {
        document.getElementsByClassName("webgl")[0].style.pointerEvents = "none";
       
        }});
    }
}