 /**
 *******************************@TICK
 */


 export function animate() {
    if (!isAnimating) {
        return; 
    }
/*     materialsh.uniforms.time.value = performance.now() / 100; */
cloud.rotation.y = - performance.now() / 15000;

				cloud.material.uniforms.frame.value ++;

    if(!observing){
    raycaster.setFromCamera( mouse, camera );
    const objectsToTest = [esferaGroup.esfera1,esferaGroup.esfera2,esferaGroup.esfera3,esferaGroup.esfera4];
    const intersects = raycaster.intersectObjects(objectsToTest);
    //ESTO SE ACTIVA CUANDO NINGUNA ESFERA ESTA SIENDO HOVEREADA
    for(const object of objectsToTest){object.material.color.set("#ff0000");}
    //ESTO SE ACTIVA CUANDO UNA ESFERA ESTA SIENDO HOVEREADA
    for(const intersect of intersects){intersect.object.material.color.set("#0000ff");}
    if (intersects.length)
    {
      if(currentIntersect===null)
        {
            if(intersects[0].object.clicked==false)
            {
            intersects[0].object.hovered = true;
                hoverColorAndShowPlane(intersects[0].object,esferaGroup);
              
            
            }
        }
        currentIntersect = intersects[0];
            
     }
     //ESTO SE ACTIVA CUANDO EL CURSOR DEJA DE HOVEREAR UNA ESFERA
  else
  {
    if(currentIntersect)
      {
        if(currentIntersect.object.clicked==false ){
            currentIntersect.object.hovered = false;
            hoverColorAndShowPlane(currentIntersect.object,esferaGroup);
           
        }
        
      }
    currentIntersect=null;
    hoverColorAndShowPlane(null,esferaGroup);
  }
}
 //----------------------------------------------------- 
 //----------------------------------------------------- 
 //-----------------------------------------------------
 
     // Actualiza las partculas
     var positions = particles.geometry.attributes.position.array;
   
 
     for (var i = 0; i < positions.length; i+=3) {
         positions[i+1] += 0.01; // Cambia esto para ajustar la velocidad
 
         // Si la partcula ha llegado al tope, reinicia su posicin
         if (positions[i+1] >= 10) {
             positions[i+1] = -10;
         }
     }
 
     // Colores de actualizacin y posiciones
    particles.geometry.attributes.position.needsUpdate = true;
    //css3dw.renderercss.render(css3dw.cssScene, camera);
    efectos.composer.render();
    requestAnimationFrame( animate );
     // Renderizar la escena CSS3D
    
}

