import * as THREE from 'three';
//---------Import post-Effects ---------------------
import {UnrealBloomPass} from 'three/examples/jsm/postprocessing/UnrealBloomPass.js';
import {EffectComposer} from 'three/examples/jsm/postprocessing/EffectComposer.js';

import {RenderPass} from 'three/examples/jsm/postprocessing/RenderPass.js';
import {ShaderPass} from 'three/examples/jsm/postprocessing/ShaderPass.js';
import {RGBShiftShader} from 'three/examples/jsm/shaders/RGBShiftShader.js';
import{VignetteShader} from 'three/examples/jsm/shaders/VignetteShader.js';
import {SMAAPass} from 'three/examples/jsm/postprocessing/SMAAPass.js'; 


export class EfectosPost {
    
    constructor() {
        this.rgbPass; 
        this.vignettePass;
        this.smaaPass;
        this.bloom;
        this.composer;
        this.renderPass;
    }

 
    loadEffects(scene, camera, renderer) {
    new Promise((resolve, reject) => {
  
        //Composer
        this.composer = new EffectComposer(renderer);
        this.renderPass = new RenderPass( scene, camera );
        this.composer.addPass( this.renderPass );
        
        //CromaticAberration
        this.rgbPass= new ShaderPass(RGBShiftShader);
        this.composer.addPass(this.rgbPass);
        this.rgbPass.material.uniforms.amount.value=0.0016;
        this.rgbPass.material.uniforms.angle.value=10;
        this.rgbPass.enabled=true;

        //BLOOM
        this.bloom = new UnrealBloomPass(new THREE.Vector2(window.innerWidth, window.innerHeight),0.30,1,0.2);
        this.composer.addPass(this.bloom);
        this.bloom.enabled=true;
        
        //vignette
        this.vignettePass= new ShaderPass(VignetteShader);
        this.composer.addPass(this.vignettePass);
        this.vignettePass.enabled=true;

        //SMAA
        this.smaaPass= new SMAAPass( window.innerWidth * renderer.getPixelRatio(), window.innerHeight * renderer.getPixelRatio() );
        this.composer.addPass(this.smaaPass);
        this.smaaPass.enabled=true;
        
        
        return resolve();
    });
}


}
