



export function convertirEsferasArrojasExcepto(esfera_clicked, plane_showed, esferaGroup){
    //Convertir esferas arrojas excepto la esfera clickeada y ocultar los planos excepto el de la esfera clickeada
    if(esfera_clicked===esferaGroup.esfera1){
        esferaGroup.esfera2.material.color.set("#ff0000");
        esferaGroup.esfera3.material.color.set("#ff0000");
        esferaGroup.esfera4.material.color.set("#ff0000");
        esferaGroup.esfera2.clicked = false;
        esferaGroup.esfera3.clicked = false;
        esferaGroup.esfera4.clicked = false;
        esferaGroup.plane2.visible = false;
        esferaGroup.plane3.visible = false;
        esferaGroup.plane4.visible = false;

    }
    else if(esfera_clicked===esferaGroup.esfera2){
        esferaGroup.esfera1.material.color.set("#ff0000");
        esferaGroup.esfera3.material.color.set("#ff0000");
        esferaGroup.esfera4.material.color.set("#ff0000");
        esferaGroup.esfera1.clicked = false;
        esferaGroup.esfera3.clicked = false;
        esferaGroup.esfera4.clicked = false;
        esferaGroup.plane1.visible = false;
        esferaGroup.plane3.visible = false;
        esferaGroup.plane4.visible = false;

    }
    else if(esfera_clicked===esferaGroup.esfera3){
        esferaGroup.esfera1.material.color.set("#ff0000");
        esferaGroup.esfera2.material.color.set("#ff0000");
        esferaGroup.esfera4.material.color.set("#ff0000");
        esferaGroup.esfera1.clicked = false;
        esferaGroup.esfera2.clicked = false;
        esferaGroup.esfera4.clicked = false;
        esferaGroup.plane1.visible = false;
        esferaGroup.plane2.visible = false;
        esferaGroup.plane4.visible = false;
    }
    else if(esfera_clicked===esferaGroup.esfera4){
        esferaGroup.esfera1.material.color.set("#ff0000");
        esferaGroup.esfera2.material.color.set("#ff0000");
        esferaGroup.esfera3.material.color.set("#ff0000");
        esferaGroup.esfera1.clicked = false;
        esferaGroup.esfera2.clicked = false;
        esferaGroup.esfera3.clicked = false;
        esferaGroup.plane1.visible = false;
        esferaGroup.plane2.visible = false;
        esferaGroup.plane3.visible = false;
    }
    plane_showed.visible = true;
    esfera_clicked.material.color.set("#0000ff");
    esfera_clicked.clicked = true;
}

export function hoverColorAndShowPlane(esfera_hovered, esferaGroup){
    //Cambiar color de esfera que esta siendo hovereada y mostrar el plano de la esfera que esta siendo hovereada


    if(esfera_hovered===esferaGroup.esfera1){
        esferaGroup.esfera2.material.color.set("#ff0000");
        esferaGroup.esfera3.material.color.set("#ff0000");
        esferaGroup.esfera4.material.color.set("#ff0000");
        esferaGroup.esfera2.clicked = false;
        esferaGroup.esfera3.clicked = false;
        esferaGroup.esfera4.clicked = false;
        esferaGroup.plane1.visible = true;
        esferaGroup.plane2.visible = false;
        esferaGroup.plane3.visible = false;
        esferaGroup.plane4.visible = false;

    }
    else if(esfera_hovered===esferaGroup.esfera2){
        esferaGroup.esfera1.material.color.set("#ff0000");
        esferaGroup.esfera3.material.color.set("#ff0000");
        esferaGroup.esfera4.material.color.set("#ff0000");
        esferaGroup.esfera1.clicked = false;
        esferaGroup.esfera3.clicked = false;
        esferaGroup.esfera4.clicked = false;
        esferaGroup.plane1.visible = false;
        esferaGroup.plane2.visible = true;
        esferaGroup.plane3.visible = false;
        esferaGroup.plane4.visible = false;
 
    }
    else if(esfera_hovered===esferaGroup.esfera3){
        esferaGroup.esfera1.material.color.set("#ff0000");
        esferaGroup.esfera2.material.color.set("#ff0000");
        esferaGroup.esfera4.material.color.set("#ff0000");
        esferaGroup.esfera1.clicked = false;
        esferaGroup.esfera2.clicked = false;
        esferaGroup.esfera4.clicked = false;
        esferaGroup.plane1.visible = false;
        esferaGroup.plane2.visible = false;
        esferaGroup.plane3.visible = true;
        esferaGroup.plane4.visible = false;
    }
    else if(esfera_hovered===esferaGroup.esfera4){
        esferaGroup.esfera1.material.color.set("#ff0000");
        esferaGroup.esfera2.material.color.set("#ff0000");
        esferaGroup.esfera3.material.color.set("#ff0000");
        esferaGroup.esfera1.clicked = false;
        esferaGroup.esfera2.clicked = false;
        esferaGroup.esfera3.clicked = false;
        esferaGroup.plane1.visible = false;
        esferaGroup.plane2.visible = false;
        esferaGroup.plane3.visible = false;
        esferaGroup.plane4.visible = true;
    }

    else{
        if(esferaGroup.esfera1.clicked){
            esferaGroup.plane1.visible = true;
            esferaGroup.esfera1.material.color.set("#0000ff");
        }else{
            esferaGroup.plane1.visible = false;
            esferaGroup.esfera1.material.color.set("#ff0000");
        }
        if(esferaGroup.esfera2.clicked){
            esferaGroup.plane2.visible = true;
            esferaGroup.esfera2.material.color.set("#0000ff");
        }else{
            esferaGroup.plane2.visible = false;
            esferaGroup.esfera2.material.color.set("#ff0000");
        }
        if(esferaGroup.esfera3.clicked){
            esferaGroup.plane3.visible = true;
            esferaGroup.esfera3.material.color.set("#0000ff");
        }else{
            esferaGroup.plane3.visible = false;
            esferaGroup.esfera3.material.color.set("#ff0000");
        }
        if(esferaGroup.esfera4.clicked){
            esferaGroup.plane4.visible = true;
            esferaGroup.esfera4.material.color.set("#0000ff");
        }else{
            esferaGroup.plane4.visible = false;
            esferaGroup.esfera4.material.color.set("#ff0000");
        }




    }


}

export function disableOverflow() {
    document.body.style.overflow = 'hidden';
}

export function enableOverflow() {
    document.body.style.overflow = 'visible';
    //hide webgl
/*     document.getElementsByClassName("webgl")[0].style.display = "none"; */
/* document.getElementsByClassName("webgl")[0].style.opacity = 0.5;  */
  /*   document.getElementsByClassName("webgl")[0].style.pointer-events= none; */
  document.getElementsByClassName("webgl")[0].style;
}



export function resetall(esferaGroup){
    esferaGroup.esfera1.material.color.set("#ff0000");
    esferaGroup.esfera2.material.color.set("#ff0000");
    esferaGroup.esfera3.material.color.set("#ff0000");
    esferaGroup.esfera4.material.color.set("#ff0000");
    esferaGroup.esfera1.clicked = false;
    esferaGroup.esfera2.clicked = false;
    esferaGroup.esfera3.clicked = false;
    esferaGroup.esfera4.clicked = false;
    esferaGroup.plane1.visible = false;
    esferaGroup.plane2.visible = false;
    esferaGroup.plane3.visible = false;
    esferaGroup.plane4.visible = false;

}



