import * as THREE from 'three';
import { CSS3DRenderer, CSS3DObject } from 'three/examples/jsm/renderers/CSS3DRenderer.js';


export class Css3dworld {
    
    constructor() {
        this.renderercss=null;
        this.element=null;
        this.cssObject=null;
        this.cssScene= null;
    }

 
  loadCss3dWorld() {
    new Promise((resolve, reject) => {
        this.renderercss = new CSS3DRenderer();
        this.renderercss.setSize(window.innerWidth, window.innerHeight);
        document.body.appendChild( this.renderercss.domElement );
        
        return resolve();
    });
}

loadCss3dWorldObjects() {
    new Promise((resolve, reject) => {
        this.cssScene= new THREE.Scene();
    this.element = document.getElementById('elemento');

    if (this.element) {
    // Manipular el elemento aqu
    this.element.textContent = "Sistemas de Video vigilancia (IP, anlogos e hbridos), manejamos las analiticas de video mas actuales y la mas alta definicion en nuestras camaras.";
    this.element.style.color = '#ffffff';
    this.element.style.height = '5px';
    this.element.style.width = '5px';
    this.element.style.background = '#05f';
    this.element.style.fontSize = '1px';
    this.renderercss.domElement.style.position = 'absolute';
    this.renderercss.domElement.style.zIndex = '500';
    this.cssObject = new CSS3DObject(this.element);
    this.cssObject.position.x = -2;
    this.cssObject.position.y = 0;
    this.cssObject.position.z = -100;
    this.cssScene.add(this.cssObject);
    } 
    else 
    {
    console.error("Elemento no encontrado!");
    }
    return resolve();
    });
  }
}
