import * as THREE from 'three';

export class Esferas3D {

  addPheres(scene) {
/*     const textureLoader = new THREE.TextureLoader();
    const particleStarTexture = textureLoader.load("/textures/flag-french.jpg");particleStarTexture.offset.x = 0.09;
    particleStarTexture.wrapS = THREE.RepeatWrapping;
    particleStarTexture.wrapT = THREE.RepeatWrapping;
    particleStarTexture.repeat.set(1.6, 1.6);  */
    this.esfera1 = new THREE.Mesh(new THREE.SphereGeometry(1, 32, 32), new THREE.MeshStandardMaterial());
    this.esfera1.position.set(-2.8, 0.85, 0);
    this.esfera1.scale.set(0.3, 0.3, 0.3);
    this.esfera1.roughness = 0.5;
    this.esfera1.metalness = 1.0;
    this.esfera1.name = "esfera1";
    this.esfera1.clicked = false;
    this.esfera1.hovered = false;

    this.esfera2 = new THREE.Mesh(new THREE.SphereGeometry(1, 32, 32), new THREE.MeshStandardMaterial());
    this.esfera2.position.set(-2.2, 0.2, 0);
    this.esfera2.scale.set(0.3, 0.3, 0.3);
    this.esfera2.name = "esfera2";
    this.esfera2.clicked = false;
    this.esfera2.hovered = false;

    this.esfera3 = new THREE.Mesh(new THREE.SphereGeometry(1, 32, 32), new THREE.MeshStandardMaterial());
    this.esfera3.position.set(-1.45, -0.1, 0);
    this.esfera3.scale.set(0.3, 0.3, 0.3);
    this.esfera3.name = "esfera3";
    this.esfera3.clicked = false;
    this.esfera3.hovered = false;

    this.esfera4 = new THREE.Mesh(new THREE.SphereGeometry(1, 32, 32), new THREE.MeshStandardMaterial());
    this.esfera4.position.set(-1.45, -0.1, 0);
    this.esfera4.scale.set(0.3, 0.3, 0.3);
    this.esfera4.name = "esfera4";
    this.esfera4.clicked = false;
    this.esfera4.hovered = false;

    var geometry = new THREE.PlaneGeometry(0.8, 0.5);

    var material1 = new THREE.MeshBasicMaterial({ color: 0xffff00, side: THREE.DoubleSide }); // Amarillo
    this.plane1 = new THREE.Mesh(geometry, material1);
    this.plane1.position.set(this.esfera1.position.x, this.esfera1.position.y + 0.7, this.esfera1.position.z); // Posicionar encima de la esfera1
    this.plane1.visible = false;
    this.plane1.name = "plane1";
    this.plane1.clicked = false;

    var material2 = new THREE.MeshBasicMaterial({ color: 0x0000ff, side: THREE.DoubleSide }); // Azul
    this.plane2 = new THREE.Mesh(geometry, material2);
    this.plane2.position.set(this.esfera2.position.x, this.esfera2.position.y + 0.7, this.esfera2.position.z); // Posicionar encima de la esfera2
    this.plane2.visible = false;
    this.plane2.name = "plane2";
    this.plane2.clicked = false;

    var material3 = new THREE.MeshBasicMaterial({ color: 0x00ff00, side: THREE.DoubleSide }); // Verde
    this.plane3 = new THREE.Mesh(geometry, material3);
    this.plane3.position.set(this.esfera3.position.x, this.esfera3.position.y + 0.7, this.esfera3.position.z); // Posicionar encima de la esfera3
    this.plane3.visible = false;
    this.plane3.name = "plane3";
    this.plane3.clicked = false;

    var material4 = new THREE.MeshBasicMaterial({ color: 0xe219c3, side: THREE.DoubleSide }); // Verde
    this.plane4 = new THREE.Mesh(geometry, material4);
    this.plane4.position.set(this.esfera4.position.x, this.esfera4.position.y + 0.7, this.esfera4.position.z); // Posicionar encima de la esfera3
    this.plane4.visible = false;
    this.plane4.name = "plane4";
    this.plane4.clicked = false;

    scene.add(this.esfera1, this.esfera2, this.esfera3,this.esfera4, this.plane1, this.plane2, this.plane3, this.plane4);
  }
}
